@font-face {
    font-family: 'GangwonEdu_OTFBoldA';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/GangwonEdu_OTFBoldA.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

.html,
body {
    font-family: 'GangwonEdu_OTFBoldA';
}

/*  */

.login_outContainer {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
}

.login_innerContainer {
    width: 18rem;
    height: 25rem;
    background-color: transparent;
    position: absolute;
    left: 50%;
    top: 35%;
    transform: translate(-50%, -50%);
    z-index: 100;
    border: 1px solid white;
    text-align: center;
}

.login_img {
    margin-top: 1rem;
    width: 10rem;
}

.login_title {
    margin-top: 0rem;
    color: white;
}

.login_kakao {
    width: 14rem;
}

.login_text {
    font-size: 0.7rem;
    margin-top: 4rem;
    color: white;
}

.login_name {
    margin-top: 1rem;
    font-size: 1.0rem;
    color: darkgray;
}

/*  */

.redirect_outContainer {
    width: 100%;
    height: 100vh;
    text-align: center;
}

.redirect_img {
    width: 20rem;
    margin-top: 6rem;
}

.redirect_h3 {
    position: relative;
    top: -5rem;
}